import { BtnClose } from '../../../lib/components/btn-close/btn-close';
import { PopupStandaloneModalComp } from '../../../lib/components/popups/standalone/modal/popup-standalone-modal';

import styles from './popup-notes.scss';
import sharedStyles from '../../../styles/styles.scss';

export class PopupStandaloneModalNotes extends PopupStandaloneModalComp {
  static readonly TEXT_CONTENT_ENG = {
    HEADER: `Dear visitor!`,
    P1: `Please pay attention to the following points:`,
    OL: {
      LI1: `The server must be restarted before testing -
      a previous FE that you may have tested before could,
      for example, save colors in non-hex format,
      create machines with incredibly long names,
      leave engines enabled, etc.`,
      LI2: `Unfortunately, the server works in such a way
        that I have to take control of the machines
        before the setTimeout happens on the server.
        The fact is that the server turns off the engine in any case,
        it does not look whether the engine was turned off manually earlier
        and turned on again. As a result,
        depending on your luck, you can run into different mistakes.`,
      LI3: `The application has been tested in various scenarios in Chromium and Firefox.
        You can even try shutting down the server after starting the race.
        But if you suddenly find a bug, please write to me in discord (no95typem @ 9889).`,
      LI4: `For a nicer look, turn on glitch if you can.`,
      LI5: `I hope your hardware is not weak!
        I have a little slowdown on chromium,
        but on FF with webrender enabled, everything is fine!`,
      LI6: `By the way, if we are talking about browsers,
        please enable autoplay video permission (and reload the page)
        if you have blocked this feature for pages (as I do).
        The background on the garage page should be a playing video.`,
      LI7: `For cross checkers - if you find flaws,
        write at least in detail and do not do it anonymously, please!`,
    },
    CAPTION: `Thanks for reading!`,
  };

  protected TEXT_CONTENT = PopupStandaloneModalNotes.TEXT_CONTENT_ENG;

  protected list = document.createElement('ol');

  protected btnOk = document.createElement('button');

  protected btnCloseContainer = document.createElement('div');

  protected caption = document.createElement('p');

  constructor() {
    super();
    this.header.textContent = this.TEXT_CONTENT.HEADER;

    this.renderList();

    this.renderCloseBtns();
    this.setupCloseActions();

    this.renderOtherThings();

    this.body.classList.add(styles.body);
    this.body.append(
      this.list,
      this.btnCloseContainer,
      this.caption,
      this.btnOk,
    );
  }

  private renderCloseBtns() {
    const btnCloseComp = new BtnClose();
    this.btnCloseContainer.classList.add(styles['btn-close-container']);
    btnCloseComp.rootElement.classList.add(styles['btn-close']);
    this.btnCloseContainer.append(btnCloseComp.rootElement);

    this.btnOk.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['btn-ok'],
    );

    const btnOkOuterSpan = document.createElement('span');
    const btnOkInnerSpan = document.createElement('span');
    btnOkInnerSpan.textContent = 'OK';
    btnOkInnerSpan.dataset.textContent = 'OK';
    btnOkInnerSpan.classList.add(styles['btn-ok__innerSpan']);
    btnOkOuterSpan.append(btnOkInnerSpan);
    this.btnOk.append(btnOkOuterSpan);
  }

  private renderList() {
    const liTextsArray = Object.values(this.TEXT_CONTENT.OL);
    const liArray = liTextsArray.map(text => {
      const li = document.createElement('li');
      const span = document.createElement('span');
      span.textContent = text;
      li.append(span);
      return li;
    });
    this.list.append(...liArray);
    this.list.classList.add(styles.ol);
  }

  private renderOtherThings() {
    this.caption.textContent = this.TEXT_CONTENT.CAPTION;
  }

  private setupCloseActions() {
    this.btnCloseContainer.addEventListener('click', () => this.closeSelf());
    this.btnOk.addEventListener('click', () => this.closeSelf());
    document.body.addEventListener('keydown', e => {
      if (e.key === 'Escape') this.closeSelf();
    });
  }

  show(rootElem?: HTMLElement) {
    super.show(rootElem);
    const anim = this._rootElement.animate(
      [{ opacity: `0` }, { opacity: `1` }],
      {
        duration: 500,
        easing: 'ease-in-out',
        fill: 'forwards',
      },
    );
    anim.oncancel = () => this.clearAnimFromElem(anim);
    anim.onfinish = () => this.clearAnimFromElem(anim);
    return this;
  }

  private async closeSelf() {
    const anim = this._rootElement.animate(
      [{ opacity: `1` }, { opacity: `0` }],
      {
        duration: 500,
        easing: 'ease-in-out',
        fill: 'forwards',
      },
    );
    await anim.finished;
    this._rootElement.remove();
    this.clearAnimFromElem(anim);
    this._rootElement.style.removeProperty('opacity');
  }

  private clearAnimFromElem = (a: Animation) => {
    const anim = a as unknown as Record<string, unknown>;
    if ('commitStyles' in anim && typeof anim.commitStyles === 'function')
      anim.commitStyles();
  };
}
